import { RefObject, useEffect } from "react";

/**
 * Hook qui détecte les clics à l'extérieur d'un élément spécifié et appelle une fonction de rappel en réponse.
 * Cela peut être utile pour fermer un modal, un menu déroulant, ou d'autres éléments interactifs lorsqu'un clic en dehors est détecté.
 *
 * @param ref RefObject pointant vers l'élément DOM à surveiller.
 * @param onOutsideClick Fonction à appeler lorsque un clic en dehors de l'élément référencé est détecté.
 */
export function useClickOutside(ref: RefObject<HTMLElement>, onOutsideClick: () => void): void {
    useEffect(() => {
        if (typeof window !== "undefined") {
            const handleClickOutside = (event: MouseEvent) => {
                if (ref.current && !ref.current.contains(event.target as Node)) {
                    onOutsideClick();
                }
            };

            document.addEventListener('mousedown', handleClickOutside);
            return () => {
                document.removeEventListener('mousedown', handleClickOutside);
            };
        }
    }, [ref, onOutsideClick]);
}
