/**
 * Vérifie si la valeur fournie est considérée comme "vide".
 *
 * Une valeur est considérée comme vide si elle correspond à l'un des critères suivants :
 * - Elle est `undefined`.
 * - Elle est `null`.
 * - Elle est une chaîne de caractères et, après suppression des espaces blancs, elle est de longueur zéro.
 * - Elle est un objet et n'a aucune propriété propre (Note : cela inclut les objets littéraux, mais pas les instances de classes personnalisées qui n'ont pas de propriété énumérable).
 * - Elle est un tableau et sa longueur est zéro.
 * - Elle est une instance de `Map` ou `Set` et sa taille est zéro.
 *
 * Note : Cette fonction ne considère pas les valeurs booléennes `false`, les nombres `0`, les dates ou d'autres types primitifs comme étant "vides",
 * même si, dans certains contextes, ils pourraient être considérés comme représentant une absence de valeur.
 *
 * @param value La valeur à tester.
 * @returns `true` si la valeur est considérée comme vide selon les critères ci-dessus, sinon `false`.
 */
export const isEmpty = (value: any) => {
    return (
        value === undefined ||
        value === null ||
        (typeof value === "string" && value.trim().length === 0) ||
        (typeof value === "object" && Object.keys(value).length === 0) ||
        (Array.isArray(value) && value.length === 0) ||
        (value instanceof Map && value.size === 0) ||
        (value instanceof Set && value.size === 0)
    );
};